import React, { useState } from "react";
import Axios from "axios";
import { Container, Form, Image } from "react-bootstrap";

const Champion_URL =
  "http://ddragon.leagueoflegends.com/cdn/10.19.1/data/en_US/champion.json";

const App = () => {
  const [loaded, setLoaded] = useState(false);
  const [champNames, setchampNames] = useState([]);
  const [filter, setFilter] = useState("");
  const [skins, setSkins] = useState([]);

  if (!loaded) {
    Axios.get(Champion_URL)
      .then((res) => res.data.data)
      .then((data) => {
        let datas = [];
        for (const champ in data) {
          datas.push(champ);
        }
        return datas;
      })
      .then((datas) => setchampNames(datas));
    setLoaded(true);
  } else {
    return (
      <Container>
        <h1>League of Legends Image Gallery</h1>
        <Form>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Filter </Form.Label>
            <Form.Control
              onChange={(e) => setFilter(e.target.value)}
              type="text"
              placeholder=""
            />
          </Form.Group>
        </Form>
        {champNames
          .filter((val) => val.toLowerCase().includes(filter.toLowerCase()))
          .map((name) => (
            <Image
              onClick={(event) => {
                const champ = event.target.alt;
                fetch(
                  "http://ddragon.leagueoflegends.com/cdn/10.19.1/data/en_US/champion/" +
                    champ +
                    ".json"
                )
                  .then((response) => response.json())
                  .then((data) => {
                    const champSkinList = data.data[champ].skins;
                    const champSkins = [];
                    champSkinList.forEach((element) => {
                      champSkins.push(element.num);
                    });
                    const skins = [];
                    champSkins.forEach((val) =>
                      skins.push(
                        "http://ddragon.leagueoflegends.com/cdn/img/champion/splash/" +
                          champ +
                          "_" +
                          val +
                          ".jpg"
                      )
                    );
                    console.log(skins);
                    setSkins(skins);
                  });
              }}
              key={name}
              alt={name}
              src={
                "http://ddragon.leagueoflegends.com/cdn/10.19.1/img/champion/" +
                name +
                ".png"
              }
            />
          ))}
        <Container>
          {skins.map((skin) => (
            <Image src={skin} width="608" height="359" />
          ))}
        </Container>
      </Container>
    );
  }

  return (
    <Container>
      <div>Loading...</div>
    </Container>
  );
};
export default App;
